* {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  background-color: #000000; 
  max-width: 800px;
  margin:0 auto;
}

.page {    
  background-color: #ffffff; 
  padding: 20px;
  padding-bottom: 250px;        
  margin: auto;
}
  
.header {    
  display: flex;
  background-color: #f2f2f2; 
  justify-content: space-between;
  align-items: center;
  width: calc(100% + 40px); 
  min-height: 200px;
  padding: 20px;
  margin: -20px -20px 0 -20px;
}

p {
  line-height: 1.5;
  margin: 0;
}

h2.section-header {
  font-size: 18px;
  font-style: italic;
  font-weight: bold;
  flex-shrink: 0;
  margin-top: 10;
  margin-right: 30px;
  margin-left: 15px;
  margin-bottom: 15px;
}

h3 {
  font-weight: bold;
  font-style: normal;
  font-size: 16px;
  margin-bottom: 5px;
}

.personal-info {
  margin-left:20px; 
}

.personal-info .name {
  font-size: 24px;
  font-weight: bold;    
  line-height: 0;
}

.personal-info .title {
  font-size: 21px;
}

.contact-info {
  list-style-type: none;
  padding-right: 50px;    
}

.contact-info li {
  font-size: 12px;
  line-height: 2;
}

hr.yellow-line {
  border-top: 6px solid #ffcc00; 
  margin: 0 -20px;
  margin-bottom: 20px;    
}

hr.black-line{
  width: 96%;
  margin-bottom: -15px;
}

.personal-profile {
  display: flex;
  align-items: flex-start;
  margin-bottom: 20px;
  padding-right: 50px;
}

.personal-profile p {
  padding-top: 10px;
  padding-left: 30px;
}

.work-experience {
  display: flex;
  align-items: flex-start;
  margin-top: 20px;
  margin-bottom: 20px;
}

.job-list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-left: -20px;
  margin-right: 40px;
  margin-top: 0px;
}

.key-skills {
  display: flex;
  align-items: flex-start;
  margin-top: 20px;
}

.skills-list {
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 150px);
  margin-top: 15px;
  padding-left: 80px;
}

.education {
  display: flex;
  align-items: flex-start;
  margin-top: 20px;
  margin-bottom: 20px;
}

.education-list {
  list-style-type: none;
  margin-left: 40px;
  margin-top: 0px;
}